import { useEffect, useState } from "react";
import Swal from "sweetalert2";
import FilterAltIcon from "@mui/icons-material/FilterAlt";
import "../../index.css";
import { IconButton } from "@mui/material";
import Button from '@mui/material/Button'

const SearchComponent = (props) => {
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("All");

  useEffect(() => {
    setCategories(props.categories);
  }, [props.categories]);

  const handleCategoryChange = (e) => {
    const newCategory = e.target.value;
    setSelectedCategory(newCategory);
    props.setFilterParam(newCategory);
    props.setQuery(""); // Limpiar el buscador al seleccionar una categoría
    Swal.close(); // Cerrar el modal inmediatamente después de seleccionar
  };

  const handleSearchChange = (e) => {
    const newQuery = e.target.value;
    props.setQuery(newQuery);
    if (newQuery) {
      setSelectedCategory("All"); // Reiniciar la categoría a "All" si hay búsqueda
      props.setFilterParam("All");
    }
  };

  const openFilterModal = () => {
    Swal.fire({
      title: "Filtrar por Categoría",
      html: `
        <select id="category-select" class="form-control " onchange="window.handleCategoryChange(event)">
          <option value="All">Todas las categorías 🔠</option>
          ${categories
            .slice()
            .sort((a, b) => a.categoria.localeCompare(b.categoria))
            .map((categ) => `<option value="${categ.categoria}">${categ.categoria}</option>`)
            .join("")}
        </select>
      `,
      showCancelButton: false,
      showConfirmButton: false,
      didOpen: () => {
        window.handleCategoryChange = handleCategoryChange;
      }
    });
  };

  return (
    <div className="wrapper mb-4 mt-3">
      <div className="search-wrapper col-sm-12 d-lg-flex justify-content-start align-items-center">
        <div className="d-flex border border-light rounded overflow-hidden bg-light container-search-shadow">
          <input
            type="search"
            name="search-form"
            id="search-form"
            className="form-control border-0 bg-light px-3 py-0 "
            placeholder="Buscas algo?"
            value={props.query}
            onChange={handleSearchChange}
            title="Buscar"
          />
          <IconButton
            aria-label="filtrar"
            size="large"
            className="bg-light"
            title="Filtrar por categoría"
            onClick={openFilterModal}
            style={{ color: "#3f51b5" }}
          >
            <FilterAltIcon fontSize="inherit" />
          </IconButton>
        </div>
      </div>
    </div>
  );
};

export default SearchComponent;